html {
  font-size: 50px;
}
aside {
  height: 100vh;
  background-color: #1890ff;
}
header.ant-layout-header {
  height: 1rem;
  position: relative;
  z-index: 10;
  line-height: 1rem;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
}
main.ant-layout-content {
  height: calc(100vh - 1rem);
  overflow-y: auto;
}
